/* SweetAlert */

.swal2-container {
  z-index: 1500;
}

.swal2-modal {
  min-height: 315px;
  @extend .d-flex;
  @include align-items(center);
  @include justify-content(center);
  @include flex-direction(column);
  @include border-radius(10px);
  background: rgb(38, 40, 46) !important;

  .swal2-title {
    font-size: 25px;
    line-height: 1;
    font-family: $type1-semibold;
    color: $text-muted;
    font-weight: initial;
    margin-bottom: 0;
  }

  .swal2-icon,
  .swal2-success-ring {
    margin-top: 0;
    margin-bottom: 42px;
  }

  .swal2-buttonswrapper {
    margin-top: 0;
    padding: 0;

    .swal2-styled {
      @extend .btn;
      margin-top: 0;
      font-weight: initial;

      &.swal2-confirm {
        @extend .btn;
        background-color: theme-color(success) !important;
        border: 1px solid theme-color(success) !important;
        font-weight: initial;
        margin-top: 32px;
      }

      &.swal2-cancel {
        @extend .btn;
        margin-top: 32px;
        border: 1px solid $border-color;
        color: $text-muted;
        font-weight: initial;
      }
    }
  }

  .swal2-content {
    font-size: $default-font-size;
    font-family: $type1-semibold;
    color: $text-muted;
    font-weight: initial;
    margin-top: 11px;
  }

  .swal2-close {
    font-size: 20px;
  }

  .swal2-success-ring {
    left: -30px;
  }
}

.swal2-icon.swal2-success [class^=swal2-success-circular-line] {
  background: inherit !important;
}
.swal2-icon.swal2-success .swal2-success-fix {
  background: #26282e !important;
}