/* Fonts */

//Yantramanav
@font-face {
  font-family: 'assistant-light';
  src: url('../fonts/Assistant/Assistant-Light.eot'); /* IE9 Compat Modes */
  src: url('../fonts/Assistant/Assistant-Light.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/Assistant/Assistant-Light.woff') format('woff'), /* Pretty Modern Browsers */
       url('../fonts/Assistant/Assistant-Light.ttf')  format('truetype'), /* Safari, Android, iOS */
}
@font-face {
  font-family: 'assistant-regular';
  src: url('../fonts/Assistant/Assistant-Regular.eot'); /* IE9 Compat Modes */
  src: url('../fonts/Assistant/Assistant-Regular.woff2') format('woff2'), /* Super Modern Browsers */
