/* Pws-tabs */

.pws_tabs_container {
	ul.pws_tabs_controll {
		border-bottom: 1px solid theme-color(success);
		li {
      &:first-child{
        a{
          @include border-radius(5px 0px 0px 0px);
        }
      }
      &:last-child{
        a{
          border-right: 1px solid theme-color(success);
          @include border-radius(0px 5px 0px 0px);
        }
      }
			a {
				border: 1px solid theme-color(success);
				border-bottom: none;
        border-right: none;
				background: #fff;
				color: theme-color(success);
				padding: 0.6em 1.3em;
        margin-right: 0 !important;

				&:hover {
					background: #fff;
					color: theme-color(success);
				}

				&.pws_tab_active {
					background: theme-color(success);
					color: #fff;
				}
			}
		}
	}

	.demo-tabs {
		border: 1px solid theme-color(success);
		@include border-radius(0px 5px 5px 5px);
		overflow: hidden;
		background: $black;
		margin-bottom: 45px;
		padding-top: 3.5rem;

		.CodeMirror {
			margin-bottom: 20px;
		}
	}
  // Styles for vertical Tab
  &.pws_tabs_vertical{
    .pws_tabs_controll{
      border-right: none;
			border-bottom:none;
      li{
        &:first-child{
          a{
            @include border-radius(5px 0px 0px 0px);
          }
        }
        &:last-child{
          a{
            border-bottom: 1px solid theme-color(success);
            @include border-radius(0px 0px 0px 5px);
            border-right: 0px;
          }
        }
        a{
          @include border-radius(0px);
          border: 1px solid theme-color(success);
          border-right: none;
          border-bottom: none;
          margin-bottom: 0 !important;
        }
      }
    }
  }
  // Styles for vertical Tab

  // Styles for horizontal Tab
  &.pws_tabs_horizontal_bottom{
    .pws_tabs_list{
      margin-bottom: 0;
    }
    .pws_tabs_controll{
			border-top: 1px solid theme-color(success);
      border-right: none;
			border-bottom: none;
      li{
        &:first-child{
          a{
            @include border-radius(0px 0px 0px 5px);
          }
        }
        &:last-child{
          a{
            @include border-radius(0px 0px 5px 0px);
            border-right: 1px solid theme-color(success);
          }
        }
        a{
          @include border-radius(0px);
          border: 1px solid theme-color(success);
          border-top: none;
          border-right: none;
          margin-right: 0 !important;
          margin-bottom: 0 !important;
        }
      }
    }
  }
  // Styles for vertical Tab

  // Styles for RTL Tab
  &.pws_tabs_rtl{
    .pws_tabs_controll {
  		li {
        &:first-child{
          a{
            @include border-radius(0px 5px 0px 0px);
            border-right: 1px solid theme-color(success);
          }
        }
        &:last-child{
          a{
            border-right: none;
            @include border-radius(5px 0px 0px 0px);
          }
        }
  			a {
  				border: 1px solid theme-color(success);
  				border-bottom: none;
          border-right: none;
          margin-right: 0 !important;
          margin-left: 0 !important;
  			}
  		}
  	}
  }
  // Styles for RTL Tab
}
